// pages/index.tsx

import { useState } from 'react';
import Papa from 'papaparse';

const csvUrl =
  'https://docs.google.com/spreadsheets/d/1HP_gXN9WcsOEmQpi4-ENFi39pS6fjxIzdz31gLZANJc/export?format=csv&gid=1369639130';

type DataRow = {
  id: string;
  hours: string; // adjust this type if needed
  // include additional fields if your CSV contains more columns
};

export default function Home() {
  const [inputId, setInputId] = useState('');
  const [result, setResult] = useState<DataRow | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setResult(null);

    try {
      // Fetch CSV data from the Google Sheets URL
      const response = await fetch(csvUrl);
      if (!response.ok) {
        throw new Error('Failed to fetch CSV data');
      }
      const csvText = await response.text();

      // Parse the CSV data with Papa Parse
      const parsed = Papa.parse<DataRow>(csvText, {
        header: true,
        skipEmptyLines: true,
      });
      const data = parsed.data;

      // Find the row that matches the provided ID
      const found = data.find((row) => row.id === inputId);
      if (found) {
        setResult(found);
      } else {
        setError('ID not found');
      }
    } catch (err) {
      console.error(err);
      setError('An error occurred while fetching data.');
    }
    setLoading(false);
  };

  return (
    <div style={{ padding: '2rem' }}>
      <h1>Check Your Hours</h1>
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          value={inputId}
          onChange={(e) => setInputId(e.target.value)}
          placeholder="Enter your ID"
          style={{ marginRight: '1rem' }}
        />
        <button type="submit">Check Hours</button>
      </form>
      {loading && <p>Loading...</p>}
      {error && <p style={{ color: 'red' }}>{error}</p>}
      {result && (
        <div>
          <h2>Results:</h2>
          <p>
            <strong>ID:</strong> {result.id}
          </p>
          <p>
            <strong>Hours:</strong> {result.hours}
          </p>
        </div>
      )}
    </div>
  );
}
